.container {
font-family: sans-serif;
padding: 1em;
background: #f9f9f9;
}

.controls {
margin-bottom: 1em;
}

input {
padding: 0.4em;
font-size: 1em;
}

.grid {
display: grid;
grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
gap: 1em;
}

.thumb-card {
background: white;
border-radius: 10px;
box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
overflow: hidden;
cursor: pointer;
transition: transform 0.2s;
}

.thumb-card:hover {
transform: scale(1.05);
}

.thumb-card img {
width: 100%;
height: 100px;
object-fit: cover;
}

.thumb-card p {
padding: 0.5em;
font-size: 0.9em;
text-align: center;
}

.pagination {
margin-top: 1em;
text-align: center;
}

.pagination button {
margin: 0 0.5em;
}

.modal {
position: fixed;
top: 0;
left: 0;
right: 0;
bottom: 0;
background: rgba(0, 0, 0, 0.7);
display: flex;
justify-content: center;
align-items: center;
}

.modal-content {
background: #000;
padding: 1em;
border-radius: 10px;
}

.modal-content video {
width: 80vw;
max-height: 80vh;
}

.search-bar {
      display: flex;
        align-items: center;
          gap: 8px;
            width: 100%;
              margin-bottom: 16px;
}

.search-bar input {
      flex: 1;
        padding: 10px;
          font-size: 16px;
            border: 1px solid #ccc;
              border-radius: 4px;
}

.search-bar .search-button {
      flex: 2; /* ← これで「検索ボタン」が横に広がる！ */
        padding: 10px 0;
          font-size: 16px;
            background-color: #007bff;
              color: white;
                border: none;
                  border-radius: 4px;
                    cursor: pointer;
}

.search-bar .search-button:hover {
      background-color: #0056b3;
}

.search-bar button:not(.search-button) {
      padding: 10px 16px;
        font-size: 16px;
          background-color: #ccc;
            border: none;
              border-radius: 4px;
                cursor: pointer;
}

.search-bar button:not(.search-button):hover {
      background-color: #bbb;
}

